<template>
  <div>
    <div style="display:flex;justify-content:center;">
      <input type="search"
             results="5"
             name="word"
             id="Search"
             placeholder="请输入关键字"
             v-model="searchValue"
             @keyup.enter="searchAnimation"
             class="input_border" />
      <div class="search"
           style="border-radius: 0 20px 20px 0;"
           @click="Search()">搜索</div>
    </div>
    <div v-if="businessAll.length > 0"
         class="scroll">
      <div v-for="(item, i) in businessAll"
           :key="i"
           style="background:#fff;padding:10px 0 0;border-bottom:1px solid #e6e6e6;">
        <div class="info-hotel">
          <div class="hotel-left">
            <div style="margin-right:8px;">
              <img v-if="item.headUrl"
                   :src="item.headUrl"
                   class='left-img'>
              <div v-else>
                <img :id="'Img' + item.id"
                     style="border-radius:50%;width:30px;height:30px;margin-right:6px;" />
                <canvas :id="'headImage' + item.id"
                        style="display: none"></canvas>
              </div>
            </div>
            <div class="left-title">
              <div style="display:flex;align-items:center;">
                <div class="c_000">{{ item.realName }} {{ item.phone}}</div>
                <!-- <div style="font-size:0.6rem;border:1px solid #E5E2E5;padding:2px;margin-left:8px;">美团</div> -->
              </div>
              <!-- <div class="adr">{{ item.ccc }}</div> -->
              <div class="adr">酒店数量：{{ item.hotelNum }}
              </div>
            </div>
          </div>
          <!-- <div style="border:1px solid red;"></div> -->
        </div>
        <div style="display:flex;justify-content:flex-end;padding:10px;">
          <div class="edit-reason"
               @click="goNext('/business-commission',item)">提成统计</div>
          <div class="edit-reason"
               @click="goNext('/customer-management',item)">门店列表</div>
        </div>
      </div>
      <!-- </van-list>
      </van-pull-refresh> -->
    </div>
    <div v-else
         class="nothing">
      <img src="@/assets/hotelAudit/empty.png"
           alt="">
      <!-- <div>暂无酒店</div> -->
    </div>
  </div>
</template>
<script>
import { GetSystemDeptAllUser } from '@/api/api_depart';
import { draw } from '@/utils/avatar.js';
export default {
    data() {
        return {
            listLoading: false, // 上滑列表加载（每一次上滑的时候）---加载中
            finishedList: false, // 上滑列表加载完了所有数据---没有更多了）
            pullLoading: false, // 下拉刷新加载动画---加载中
            searchValue: null,
            dataForm: {
                searchValue: null
            },
            businessAll: []
        };
    },
    created() {
        this.$store.commit('setHeader', '业务员');
        this.getCurrentInfo();
    },
    methods: {
        getCurrentInfo() {
            GetSystemDeptAllUser(this.dataForm).done((res) => {
                this.businessAll = res.data;
                this.$nextTick(function () {
                    this.businessAll.forEach((item) => {
                        if (!item.avatar) {
                            draw(40, 40, 20, 'headImage' + item.id, 'Img' + item.id, item.realName, 1);
                        }
                    });
                });
            });
        },
        searchAnimation() {
            this.dataForm.searchValue = this.searchValue;
            this.getCurrentInfo();
        },
        Search() {
            this.dataForm.searchValue = this.searchValue;
            this.getCurrentInfo();
        },
        goNext(url, item) {
            this.$router.push({
                path: url,
                query: {
                    userId: item.id
                }
            });
        }
    }
};
</script>
<style scoped>
input {
    -webkit-appearance: none !important;
    border-radius: 20px 0 0 20px !important;
    text-indent: 2em;
    margin: 10px 0;
    width: 82%;
    height: 32px;
    border: 0;
    outline: none;
    box-sizing: border-box;
}
#Search::-webkit-search-cancel-button {
    display: none;
}
.input_border {
    font-size: 0.7rem;
}
.search {
    border: none;
    font-size: 0.7rem;
    color: #fff;
    margin-top: 10px;
    display: inline-block;
    width: 15%;
    height: 32px;
    line-height: 32px;
    background-color: #ff7d41;
}
.search:active {
    opacity: 0.5;
}
.scroll {
    position: fixed;
    top: 110px;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    font-size: 0.8rem;
    overflow-y: auto;
    background: #fff;
}
.nothing {
    margin: 100px auto;
    line-height: 50px;
    font-size: 0.8rem;
}
.info-hotel {
    font-size: 0.8rem;
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
    color: #b2b2b2;
}
.hotel-left {
    display: flex;
    align-items: center;
    text-align: left;
    width: 80%;
}
.left-img {
    width: 40px;
    height: 40px;
    border-radius: 4px;
}
.left-title {
    height: 60px;
}
.hotel-right {
    display: flex;
}
.edit-reason {
    padding: 4px 8px;
    border: 1px solid #ff7d41;
    border-radius: 2px;
    color: #ff7d41;
    font-size: 0.6rem;
    margin: 0 6px;
}
.c_000 {
    color: #000;
}
.adr {
    font-size: 0.6rem;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    margin: 2px 0;
}
.edit-reason {
    padding: 4px 8px;
    border: 1px solid #c0bec1;
    border-radius: 4px;
    color: #c0bec1;
    font-size: 0.6rem;
    margin: 0 6px;
}
</style>



